import React, { Component } from 'react';
import './App.css';
import Header from './components/Header/Header.component';
import FormLoginRegister from './components/FormLoginRegister/FormLoginRegister.component';
import imageLogin from './assets/images/imageLogin.jpg';
import FormLogin from './components/FormLogin/FormLogin.component';
import './components/FormLoginRegister/FormLoginRegister.css';
import Home from './components/Home';
import SignupPage from './components/SignupPage.component';
//import app from '../../firebase/configure';
import {auth, facebookProvider, googleProvider, twitterProvider} from './firebase/configure';
import { BrowserRouter as Router, Route } from 'react-router-dom';

class App extends Component {
  constructor() {
    super();
    this.state = ({
      user: null,
      email: '',
      password: '',
      isLoading: false
    });
    this.authListener = this.authListener.bind(this);
  }

  componentDidMount() {
    this.authListener();
  }

  authListener() {
    auth.onAuthStateChanged((user) => {
      this.setState({isLoading: true});
      console.log(user);
      if (user) {
        this.setState({ user });        
        localStorage.setItem('user', user.uid);
      } else {
        this.setState({ user: null });
        localStorage.removeItem('user');
      }
      this.setState({isLoading: false});
    });
  }
  
  handleChange =(e) => {
    this.setState({ [e.target.type]: e.target.value });
  }

  login = (e) => {
    this.setState({isLoading: true});
    auth.signInWithEmailAndPassword(this.state.email, this.state.password).then((u)=>{
      this.setState({isLoading: false});
    }).catch((error) => {
        console.log(error);
      });
  }
  logout = () => {
    auth.signOut();
  }
  signup = (e) => {
    auth.createUserWithEmailAndPassword(this.state.email, this.state.password).then((u)=>{
    }).then((u)=>{console.log(u)})
    .catch((error) => {
        console.log(error);
      })
  }
  onGoogleSignIn = googleUser => {    
      console.log("google authentication");
      auth.signInWithPopup(googleProvider)
   .then((result, error) => {
    if(error){
      console.log(error)
    }
    else{

    }
   })
  };
 onFacebookSignIn = () => {
   auth.signInWithPopup(facebookProvider)
   .then((result, error) => {
    if(error){
      console.log(error)
    }
    else{

    }
   })
 }
  onTwitterSignIn = () => {
    auth.signInWithPopup(twitterProvider)
    .then((result, error) => {
      if(error){
        console.log(error)
      }
      else{

      }
    })
  }
  render () {
    return (
      <Router>
        <div className="main-wrapper-login-page">
          <div className="header-wrapper">
            <Header title="Annotate the web" />
            {this.state.user?null:<FormLoginRegister
              formLoginRegister="form-login-register"
              text="Do not have account yet?"
              register="Register"
              classRegister="register-class"
              linewrapper="register-right-up-corner"
            />}
          </div>
          <Route
            exact
            path = '/signup'
            render={(props)=>(
              <div className="body-wrapper">
                <div className="image-wrapper">
                  <img src={imageLogin} className="login-page-image" alt="disc top computer and coup "/>
                </div>
                {this.state.user?<Home />:<FormLogin
                  handleChange = {this.handleChange}
                  login = {this.login}
                  signup = {this.signup}
                  onGoogleSignIn = {this.onGoogleSignIn}
                  onFacebookSignIn = {this.onFacebookSignIn}
                  onTwitterSignIn = {this.onTwitterSignIn} 
                />}
              </div>
            )}
          />
          <Route path='/Register' component={SignupPage} />
        </div>
      </Router>
    );
  }
}
  
 

 export default App;
